<script>
    import {Router, Link, Route} from "svelte-routing";
    import Navbar from "./component/Navbar.svelte";
    import Ward from "./component/WardTable.svelte";
    import EntityPage from "./component/EntityPage.svelte";
    import WardTable from "./component/WardTable.svelte";
    import EditPage from "./component/EditPage.svelte";
    import WardEdit from "./component/WardEdit.svelte";
    import CreatePage from "./component/CreatePage.svelte";
    import WardCreate from "./component/WardCreate.svelte";
    import DiagnosTable from "./component/DiagnosTable.svelte";
    import DiagnosCreate from "./component/DiagnosCreate.svelte";
    import DiagnosEdit from "./component/DiagnosEdit.svelte";
    import PeopleTable from "./component/PeopleTable.svelte";
    import PeopleCreate from "./component/PeopleCreate.svelte";
    import PeopleEdit from "./component/PeopleEdit.svelte";

    import {onMount} from 'svelte';
    import {USER} from './store.js';
    import {doQuery} from './utils.js';
    import Registration from "./route/Registration.svelte";
    import Main from "./route/Main.svelte";

    let mounted = false;

    onMount(async () => {
        doQuery('/user')
                .then(response => response.json())
                .then(json => {
                    USER.set(json);
                })
                .catch(() => {
                    USER.set(null);
                })
                .finally(() => {
                    mounted = true;
                });
    });
</script>

<svelte:head>
    <link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css">
</svelte:head>

{#if mounted}
    <header>
        <Navbar/>
    </header>

    <main>
        <Router>
            <Route path="/diagnosis">
                <EntityPage name="Diagnosis">
                    <div slot="table">
                        <DiagnosTable/>
                    </div>
                </EntityPage>
            </Route>

            <Route path="/diagnosis_create" let:params>
                <CreatePage name={"Diagnos"}>
                    <div slot="fields">
                        <DiagnosCreate/>
                    </div>
                </CreatePage>
            </Route>

            <Route path="/diagnosis/:id" let:params>
                <EditPage name="Diagnos">
                    <div slot="fields">
                        <DiagnosEdit id={params.id}/>
                    </div>
                </EditPage>
            </Route>

            <Route path="/wards">
                <EntityPage name="Wards">
                    <div slot="table">
                        <WardTable/>
                    </div>
                </EntityPage>
            </Route>

            <Route path="/wards_create">
                <CreatePage name={"Ward"}>
                    <div slot="fields">
                        <WardCreate/>
                    </div>
                </CreatePage>
            </Route>

            <Route path="/wards/:id" let:params>
                <EditPage name="Wards">
                    <div slot="fields">
                        <WardEdit id={params.id}/>
                    </div>
                </EditPage>
            </Route>

            <Route path="/people">
                <EntityPage name="People">
                    <div slot="table">
                        <PeopleTable/>
                    </div>
                </EntityPage>
            </Route>

            <Route path="/people_create">
                <CreatePage name={"People"}>
                    <div slot="fields">
                        <PeopleCreate/>
                    </div>
                </CreatePage>
            </Route>

            <Route path="/people/:id" let:params>
                <EditPage name="Diagnos">
                    <div slot="fields">
                        <PeopleEdit id={params.id}/>
                    </div>
                </EditPage>
            </Route>

            <Route path="/registration" component={Registration}/>

            <Route path="/" component={Main}/>
        </Router>
    </main>
{/if}

<style>
    main {
        margin-top: 1em;

    }
</style>